.news-preview { 
	margin-bottom: 20px;
  &__container {
		display: flex;
	  justify-content: space-between;
	  align-items: center;
    margin: 0 auto;
	  flex-wrap: wrap;
	  padding: 20px;
	  margin-bottom: 30px;
  	@include tablet {
	    flex-direction: column;
	    justify-content: center;
	    align-items: center;
	    margin-bottom: 0;
	  }
  }
  &__block {
    margin-right: 10px;
    margin-bottom: 20px;
    max-width: 230px;
    &:hover {
    	box-shadow: $shadow-down;
    }
    &:hover .news-preview__img-wrapper, &:hover .news-preview__primal {
			background: $second;
			color: $accent;
		}
    @include tablet {
      margin-right: 0;
    }
  }
	&__main {
		width: 100%;
		&-link {
			display: block;
		}
	}
	&__img {
		max-width: 100%;
		&-wrapper {
			margin-bottom: -1px;
			background: $accent;
			width: 100%;
			text-align: center;
		}
	}
	&__primal {
		padding: 10px;
		background: $accent;
		color: $white;
		text-align: left;
		&-title {
			font-size: 16px;
			margin-bottom: 15px;
			@include laptop {
				font-size: 14px;
			}
		}
	}
	&__date {
		font-size: 15px;
		@include laptop {
			font-size: 14px;
		}
	}
	&__btn {
		margin: 0 auto;
		font-size: 18px;
		@include laptop {
			font-size: 16px;
		}
		@include mobileL {
			font-size: 14px;
		}
		&-wrapper {
			width: 100%;
			text-align: center;
		}
	}
}