.experts-block {
	&__slider {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;
	}
}

.experts-card {
	&__container {
		margin-right: 20px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__img-wrapper {
		width: 100%;
		margin-bottom: -5px;
	}
	&__img {	
		max-width: 100%;
	}
	&__body {
    padding: 5px;
    text-align: center;
    background-color: #E8E8E8;
    border: 1px solid #000;
    word-wrap: break-word;
    min-height: 86px;
		& a {
			display: block;
		}
	}
	&__title {
		margin-bottom: 5px;
		vertical-align: middle;
		@include laptop {
			font-size: 18px;
		}
	}
	&__position {

	}
}