.breadcrumbs {
  padding: 10px;
  &--bg {
  	background: $second;
  	padding-top: 20px;
  }
	&__list {
		display: flex;
		@include tablet {
			flex-wrap: wrap;
		}
	}
	&__item {
		margin-right: 15px;
		&+.breadcrumbs__item:before {
			content: "/\00a0";
		  color: $accent;
		  margin-right: 5px;
		}
	}
	&__link {
		color: $accent;
	}
}