.services {
	&__container {
		width: 100%;
		margin-bottom: 33px;
	}
	&__block {
		width: 100%;
		max-width: 1000px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 auto 20px auto;
		padding-bottom: 10px;
		border-bottom: 2px solid $accent;
		&--title {
			margin-bottom: 15px;
			border-bottom: none;
			font-size: 20px;
			font-weight: 700;
			color: $accent;
			@include tabletL {
				font-size: 16px;
			}
		}
	}
	&__col {
		width: 50%;
		text-align: center;
	}
	&__text {
		font-size: 17px;
		@include tabletL {
			font-size: 15px;
		}
		@include mobileL {
			font-size: 14px;
		}
	}
	&__name {
		font-weight: 700;
	}
	&__price {
		font-weight: 400;
	}
	&__btn {
		margin: 0 auto;
		font-size: 17px;
		padding: 15px;
	}
}