.property-cards-filter {
	margin: 0 auto 20px auto;
  padding: 15px;
  border: 2px solid $accent;
  @include mobileL {
  	padding: 5px;
  	margin-top: -4px;
  	margin-bottom: 20px;
  	background: $white;
  }
	&__buttons {
		display: flex;
		align-items: center;
		justify-content: space-around;
		@include mobileL {
			font-size: 14px;
		}
	}
	&__sort {
		font-size: 20px;
		text-transform: uppercase;
		color: $accent;
		font-weight: 700;
		@include tabletS {
			display: none;
			font-size: 14px;
		}
	}
	&__btn {
		@include mobileL {
			padding: 3px;
			font-size: 12px;
		}
	}
}

.property-filters {
	width: 100%;
	background: $second;
	margin: 0 auto 20px auto;
	@include mobileL {
		margin-bottom: 10px;
	}
	&__top {
		display: flex;
	}
	&__title {
		text-align: center;
		font-size: 18px;
		margin-bottom: 14px;
		font-weight: 700;
		text-transform: uppercase;
		color: $accent;
	}
	&__main {
		&-list {
			font-size: 15px;
			margin-bottom: 10px;
			text-transform: uppercase;
			display: flex;
			justify-content: space-around;
			@include mobileL {
				font-size: 14px;
			}
		}
		&-item {
		}
		&-btn {
			cursor: pointer;
			padding: 5px;
		}
	}
	&-tab-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		text-transform: uppercase;
		white-space: nowrap;
		padding: 5px;
		margin-bottom: 20px;
		@include tablet {
			flex-direction: column;
		}
		@include mobileL {
			padding: 0;
			flex-direction: column;
		}
		&__item-main {
			width: 33%;
			text-align: center;
			font-weight: 400;
			cursor: pointer;
			@include tablet {
				margin-bottom: 10px;
				width: 100%;
			}
			@include mobileL {
				font-size: 14px;
			}
		}
		&__item {
			cursor: pointer;
			color: $black;
			&--active {
				color: $accent;
			}
		}	
		&__dropdown {
			display: none;
			font-weight: 300;
		}
	}
	&-tab-content {
		width: 100%;
		display: none;
	}
	&__container {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-around;
	}
	&__fieldset {
		text-align: center;
		margin-bottom: 20px;
	}
	&__block {
		text-transform: uppercase;
		font-size: 16px;
		padding: 10px;
		width: 30%;
		margin: 0 0 10px 0;
    background: $white;
    border: 1px solid $black;
    color: $black;
		@include tabletM {
			width: 50%;
			margin-right: 5px;	
		}
		@include tabletS {
			margin-right: 0;
			width: 100%;
		}
		&--fl {
			width: 100%;
		}
		&--p {
			padding: 0;
			background: transparent;
    	border: none;
		}
		&--mini {
			display: inline-flex;
			padding: 0;
		}
	}
	&__block-control {
		position: relative;
		width: 50%;
		font-size: 18px;
		padding: 10px;
		text-align: center;
		&:first-child {
			border-right: 1px solid $black;
		}
	}
	&__item {
		width: 100%;
		height: 100%;
		font-size: 18px;
		&--ph {
			width: initial;
			margin-bottom: 14px;
		}
	}
	&__btns {
		display: flex;
	}
	&__btn {
		width: 100%;
		margin-right: 10px;
		&:last-child {
			margin-right: 0;
		}
	}
}