.card {
	background: $second;
	&-top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px;
		@include tabletL {
			flex-direction: column;
		}
		&__left {
			display: flex;
			@include tabletL {
				margin-bottom: 20px;
			}
		}
	}
	&__title {
		text-transform: uppercase;
		text-align: left;
		font-size: 20px;
		@include mobileL {
			font-size: 18px;
		}
	}
	&-info {
		display: inline-flex;
    align-items: center;
		font-size: 24px;
		font-weight: 700;
		margin-right: 10px;
		@include tablet {
			font-size: 20px;
			justify-content: flex-start;
		}
		@include mobileL {
			font-size: 18px;
		}
		&__price {
			color: $accent;			
		}
		&__tel {
			margin-right: 40px;
			padding: 5px;
			background-color: $white;
			border: 2px solid $accent;
			&-link {
				color: $accent;
			}
	}
	}
	&-choosen {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-left: 60px;
		&__btn {
			background: none;
			@include mobileL {
				& svg {
					width: 25px;
					height: 25px;
				}
			}
			&:hover svg {
				fill: $active;
			}
		}
	}
	&__wrapper {
		display: flex;
		justify-content: space-between;
		padding: 20px;
		@include tabletL {
			align-items: center;
			flex-direction: column;
		}
	}
	&-block {
		padding: 15px;
		margin-bottom: 15px;
	}
	&-main {
		max-width: 66%;
		display: flex;
    flex-direction: column;
		@include tabletL {
			max-width: 100%;
		}
	}
	&-images {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		&__block {
			&-top {
				position: relative;
				width: 509px;
				@include tablet {
					width: initial;
				}
			}
		}
		&__link {
			position: relative;
			display: block;
			transition: .3s opacity;
			&:hover:after {
				opacity: .5;
			}
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: $accent;
				opacity: 0;
			}
		}
		&__img {
			max-width: 100%;
			margin-bottom: -4px;
		}
		&__thumbnails {
			width: 100%;
			display: flex;
			@include mobileL {
				display: none;
			}
			&-block {
				padding: 10px;
			}
		}
		&__btn {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 70px;
			height: 70px;
			@include tabletS {
				width: 40px;
				height: 40px;
			}
		}
	}
	&-content {
		&__object-container {
			background: $second-light;
		}
		&__object {
			width: 100%;
			display: flex;
			justify-content: space-between;
			@include tabletM {
				flex-direction: column;
			}
			&-col {
				width: 50%;
				margin-right: 10px;
				&:last-child {
					margin-right: 0;
				}
				@include tablet {
					margin-right: 0;
					width: 100%;
				}
			}
			&-row {
				width: 100%;
				font-weight: 700;
				margin-bottom: 10px;
				font-size: 18px;
				@include tablet {
					width: 100%;
					font-size: 16px;
				}
				& span {
					font-weight: 400;
				}
			}
		}
		&__descr {
			background: $white;
		}
		&__title {
			font-size: 18px;
			margin-bottom: 15px;
			@include mobileL {
				font-size: 14px;
			}
		}
		&__text {
			font-size: 18px;
		}
	}
	&-similar {
		&__container {
			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;
		}
		&__block {
			position: relative;
			margin-bottom: 10px;
			max-width: 1000px;
			width: 48%;
			@include tablet {
				width: 100%;
				padding: 0 10px;
			}
		}
	}
	&-aside {
		max-width: 33%;
		width: 100%;
		@include tabletL {
			max-width: 100%;
		}
		&__map {
			border: 1px solid $black;
			background: $white;
		}
		&__rieltor {
			max-width: 210px;
    	margin: 0 auto;
			background: $white;
		}
	}
}

.card-edit {
	background: $second;
	&__title {
		margin-bottom: 20px;
	}
}

.card-new{
	background: $second;
	&__title {
		margin-bottom: 20px;
	}
}