.personal-area {
	&__breadcrumbs {
		margin-bottom: 10px;
	}
	&-tab-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    @include tablet {
    	justify-content: flex-start;
    	overflow-x: auto;
    	margin-bottom: 20px;
    }
		&__btn {
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 15px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&-tab-content {
		display: none;
	}
	&-section {
		width: 100%;
		padding: 10px;
		&__top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
			@include tabletS {
				flex-direction: column;
			}
		}
		&__bottom {
			text-align: left;
			margin-bottom: 10px;
		}
		&__flex-container {
			display: flex;
			@include tabletL {
				flex-direction: column;
				align-items: center;
			}
		}
		&__aside {
			position: relative;
			margin-right: 50px;
			&:after {
				content: '';
				position: absolute;
				top: 0;
				right: -10%;
				height: 100%;
				width: 2px;
				background: $accent;
			}
			@include tabletL {
				margin-right: 0;
				margin-bottom: 15px;
				&:after {
					display: none;
				}
			}
		}
		&__main {
			width: 100%;
		}
		&__img-block {
			position: relative;
			width: 200px;
			height: 200px;
			background-image: url(../img/cards/card.jpg);
		  background-repeat: no-repeat;
		  background-position: center;
		  background-size: cover;
		  @include mobileL {
		  	width: 160px;
		  	height: 160px;
		  }
		}
		&__img-btn {
			position: absolute;
			bottom: 0;
			right: 0;
		}
		&__title {
			color: $accent;
			font-weight: 700;
			font-size: 15px;
			margin-bottom: 10px;
		}
		&__name {
			color: $accent;
			font-size: 16px;
			font-weight: 700;
			text-transform: uppercase;
			@include mobileL {
				font-size: 14px;
				margin-bottom: 10px;
			}
		}
		&__quantity {
			color: $accent;
			font-size: 16px;
			font-weight: 700;
			@include tabletS {
				margin-bottom: 20px;
			}
		}
		&__btn {
			padding: 8px 20px;
			font-size: 16px;
			border-radius: 50px;
			@include tablet {
				padding: 12px 26px;
    		font-size: 14px;
			}
		}
		&__link {
			cursor: pointer;
			color: $accent;
			font-size: 15px;
			& svg path {
				fill: $accent;
			}
		}
		&__clients {
			&-list {
				max-width: 300px;
				margin-bottom: 10px;
			}
			&-item {
				margin-bottom: 10px;
				padding: 10px;
				border-bottom: 2px solid $accent;
			}
			&-dropdown {
				display: none;
				margin-left: 10px;
				font-size: 16px;
				@include mobileL {
					font-size: 14px;
				}
				& a {
					color: $accent;
				}
			}
		}
	}
}